import React from 'react';
import PropTypes from 'prop-types';
import { ThemeProvider } from 'styled-components';
import CookieConsent from 'react-cookie-consent';
import { Link } from "gatsby";
import styled from 'styled-components';

import theme from '@styles/theme';
import GlobalStyles from '@styles/GlobalStyles';

const Layout = ({ children }) => (
  <ThemeProvider theme={theme}>
    <>
      <GlobalStyles />
      {children}
      <CookieConsent
          location="bottom"
          buttonText="Accept"
          declineButtonText="Decline"
          cookieName="gatsby-gdpr-facebook-pixel"
          style={{
            padding: "10px 15px 25px 15px",
            background: "#010101",
          }}
          buttonStyle={{ margin: "0", padding: "15px", background: "#775DF4", color: "#ffffff"}}
          declineButtonStyle={{padding: "15px",background: "#212121", color: "#ffffff"}}
          enableDeclineButton>
      <small>This website stores cookies on your computer. These cookies are used to collect information about how you interact with this website and allow us to remember you.
      We use this information in order to improve and customize your browsing experience and for analytics and metrics about our visitors on this website. Please learn more by checking our <ConsentLink><Link to="legality/privacy">Privacy Policy</Link></ConsentLink>.
      <br/>
      If you decline, your information won’t be tracked when you visit this website. A single cookie will be used in your browser to remember your preference not to be tracked.
      </small>
      </CookieConsent>
    </>
  </ThemeProvider>
);

Layout.propTypes = {
  children: PropTypes.node.isRequired,
};

export default Layout;


const ConsentLink = styled.a`
  a {
    color: white !important;
  }
`