import styled from 'styled-components';

export const Container = styled.div`
  max-width: 2000px;
  width: 100%;
  margin: 0 auto;
  padding: 0 16px;

  @media (min-width: ${props => props.theme.screen.xs}) {
    max-width: 540px;
  }

  @media (min-width: ${props => props.theme.screen.sm}) {
    max-width: 720px;
  }

  @media (min-width: ${props => props.theme.screen.md}) {
    max-width: 960px;
  }

  @media (min-width: ${props => props.theme.screen.lg}) {
    max-width: 1200px;
  }

  @media (min-width: ${props => props.theme.screen.xl}) {
    max-width: 2000px;
  }

  ${props =>
    props.fluid &&
    `
    max-width: 2000px !important;
  `};
`;

export const Section = styled.section`
  padding: 90px 0;
  overflow: hidden;

  background-color: ${props => props.theme.color.background.S100};
  @media (max-width: ${props => props.theme.screen.md}) {
    ${props =>
      props.padding &&
      `padding: ${
        props.padding === 'tight'
          ? "70px 0 0 0"
          : "80px 0 70px 0"
      }`};
  }
  ${props =>
    props.padding &&
    `padding: ${
      props.padding === 'tight'
        ? "90px 0 50px 0"
        : "90px 0"
    }`};
  ${props =>
    props.accent &&
    `background-color: ${
      props.accent === 'secondary'
        ? props.theme.color.grayscale.S200
        : props.theme.color.supplementary.S900
    }`};
  
`;
